import { Client } from './rest-client';
import store from '../store';

const atualizarCadastro = (usuario, callback) => {
  let body = { usuario };

  Client.put('/usuario', body)
    .then((resultado) => {
      callback(null, resultado.data);
    })
    .catch((err) => {
      callback(err);
    });
};

