// getCadastro
// Busca o cadastro do usuário utilizador.
// Para buscar o cadastro de outro usuário use getUsuario.
const getCadastro = (callback) => {
  let idUsuario = store.getters.idUsuario;

  Client.get(`/usuario/${idUsuario}`)
    .then((resultado) => {
      callback(null, resultado.data);
    })
    .catch((err) => {
      callback(err);
    });
};

// getUsuario
// Busca o cadastro de um usuário.
const getUsuario = (uid, callback) => {
  Client.get(`/usuario/${uid}`)
    .then((resultado) => {
      callback(null, resultado.data);
    })
    .catch((err) => {
      callback(err);
    });
};

// getUsuarios
// Busca a lista de usuários cadastrados.
const getListaUsuarios = (callback) => {
  Client.get('/usuario')
    .then((resultado) => {
      callback(null, resultado.data);
    })
    .catch((err) => {
      callback(err);
    });
};

const novaSenha = (senha, senhaNova, callback) => {
  let body = { senha, senhaNova };

  Client.post('/usuario/alterar-senha', body)
    .then((resultado) => {
      resultado;
      callback(null, true);
    })
    .catch((err) => {
      callback(err);
    });
};

const novoUsuario = (usuario, callback) => {
  let body = { usuario };
  Client.post('/usuario', body)
    .then((resultado) => {
      callback(null, resultado);
    })
    .catch((err) => {
      callback(err);
    });
};

export {
  atualizarCadastro,
  Estados,
  getCadastro,
  getUsuario,
  getListaUsuarios,
  novaSenha,
  novoUsuario
};
